import { Box, IconButton, Tooltip, Typography } from "@mui/material";
import * as React from "react";
import Button from "@mui/material/Button";
import profile from "../../../images/auther.PNG";
import EditIcon from "@mui/icons-material/Edit";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";
import { useNavigate } from "react-router-dom";
import EditProfile from "./EditeProfile";
import { useSelector } from "react-redux";
import { URL } from "../../../URL";
import PopUp from "../userWallet/popup";
import { profilestyle } from "./Profilecss/Customstyle";
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import TwitterIcon from "@mui/icons-material/Twitter";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import banner from "../../../images/baner2.png";

export const ProfileHeader = () => {
  const { ceilSlice } = useSelector((state) => state);
  const { userdetails } = ceilSlice;
  const navigate = useNavigate();
  const [open, setOpen] = React.useState(false);
  const array = [
    {
      name: "Facebook",
      link: userdetails?.facebook,
      icon: <FacebookIcon />,
      id: 1,
    },
    {
      name: "Instagram",
      link: userdetails?.instagram,
      icon: <InstagramIcon />,
      id: 2,
    },
    {
      name: "Twitter",
      link: userdetails?.twitter,
      icon: <TwitterIcon />,
      id: 3,
    },
    {
      name: "LinkedIn",
      link: userdetails?.linkdin,
      icon: <LinkedInIcon />,
      id: 4,
    },
  ];
  function modalClose(value) {
    setOpen(value);
  }

  return (
    <>
      {open && <EditProfile open={open} modalFunc={modalClose} />}
      <Box sx={{ width: "100%", mx: "auto", mt: { xs: 1, md: 10 } }}>
        <Button
          onClick={() => {
            navigate(-1);
          }}
          variant="outlined"
          sx={{
            border: "none",
            ml: 5,
            mb: 1,
            color: "white",
            background:
              " linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
          }}
          startIcon={<KeyboardBackspaceIcon />}
        >
          Go back
        </Button>
        <Box
          sx={{
            maxWidth: "95%",
            mx: "auto",
            borderRadius: "15px",
            mt: 3,
            "& .css-46bh2p-MuiCardContent-root": { padding: 0 },

            // boxShadow: 3,
            height: { xs: "500px", md: "450px" },
          }}
        >
          <Box
            sx={{
              position: "relative",
              backgroundImage: userdetails?.banner
                ? `url(${URL}/upload/${userdetails?.banner})`
                : `url("${banner}")`,
              height: "300px",
              backgroundSize: "cover",
              backgroundPosition: "top",
              backgroundRepeat: "no-repeat",
              borderRadius: "10px 10px",
            }}
          >
            <Box
              sx={{
                position: "absolute",
                top: "190px",
                textAlign: "center",
                left: "50%",
                transform: "translate(-50%, 20%)",
              }}
            >
              {userdetails?.profileImage ? (
                <img
                  src={`${URL}/upload/${userdetails?.profileImage}`}
                  alt=""
                  style={profilestyle}
                />
              ) : (
                <img src={profile} style={profilestyle} alt="" />
              )}

              <Typography
                sx={{
                  fontSize: "15px",
                }}
              >
                {userdetails?.name ? userdetails?.name : "user name"}
              </Typography>
            </Box>
            <Box
              sx={{
                color: "white",
                position: "absolute",
                top: "10px",
                right: "25px",
                fontSize: "20px",
              }}
            >
              {array.map((item, index) => {
                return (
                  <Tooltip title={item.name} placement="top" key={item?.id}>
                    <a href={item.link} target="_blank">
                      <IconButton
                        fontSize="small"
                        sx={{
                          background:
                            "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                          ml: 1,
                          mb: 1,
                          borderRadius: "5px",
                          width: "35px",
                          height: "35px",
                          color: "white",
                        }}
                      >
                        {item.icon}
                      </IconButton>
                    </a>
                  </Tooltip>
                );
              })}
            </Box>
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                position: "absolute",
                right: "25px",
                bottom: "-80px",
              }}
            >
              <Tooltip title="Edit" placement="top">
                <IconButton
                  sx={{
                    borderRadius: "5px",
                    background:
                      "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                    mr: 1,
                    mt: 1.5,
                  }}
                  onClick={() => {
                    setOpen(true);
                  }}
                >
                  <EditIcon fontSize="small" sx={{ color: "white" }} />
                </IconButton>
              </Tooltip>
              <PopUp />
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};
