import { Box } from "@mui/material";
import React from "react";
import { ProfileHeader } from "./profileHeader";
import ProfileTabs from "./profileTabs";
const Profile = () => {
  return (
    <>
      <Box
        sx={{
          width: "100%",
          minHeight: "100vh",
          alignItems: "start",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <Box
          sx={{
            width: { xs: "100%", md: "100%" },
            display: "flex",
            flexDirection: "column",
            fontSize: "70px",
          }}
        >
          <ProfileHeader />
          <ProfileTabs />
        </Box>
      </Box>
    </>
  );
};

export default Profile;
